import { HTTP } from '../utils/http.js'

class ResumeModel extends HTTP {
	constructor(arg){
		super()
	}
	// 
	resumeList(pageNum=1, pageSize=10, status=1, keyWord){
		return this.request({
			url: '/resume/resumeList',
			method: 'POST',
			data: {
				pageNum, 
				pageSize, 
				status, 
				keyWord
			}
		})
	}
	// 
	saveResume(
		id,
		name,
		phone,
		gender,
		age,
		educationDegree,
		workExperience,
		university,
		nativePlace,
		intentionalWork
	){
		return this.request({
			url: '/resume/saveResume',
			method: 'POST',
			data: {
				id,
				name,
				phone,
				gender,
				age,
				educationDegree,
				workExperience,
				university,
				nativePlace,
				intentionalWork
			}
		})
	}
	// 
	deleteResume(id){
		return this.request({
			url: '/resume/deleteResume',
			data: {
				id
			}
		})
	}
	//
	pushResume(jobId, resumeId, interviewDate, interviewTime,formId){
		return this.request({
			url: '/resume/pushResume',
			data: {
				jobId, 
				resumeId, 
				interviewDate, 
				interviewTime,
				formId
			}
		})
	}
	//
	pushResumeList(pageNum=1, pageSize=10, jobId, status, keyWord){
		return this.request({
			url: '/resume/pushResumeList',
			data: {
				pageNum, 
				pageSize, 
				jobId, 
				status,
				keyWord
			}
		})
	}
	// 
	resumeDetail(id){
		return this.request({
			url: '/resume/resumeDetail',
			data: {
				id
			}
		})
	}
	// 
	resumeRecord(pageNum=1, pageSize=10, id){
		return this.request({
			url: '/resume/resumeRecord',
			data: {
				pageNum,
				pageSize,
				id
			}
		})
	}
	/*** B ***/
	myCandidate(pageNum=1, pageSize=10, status, keyWord){
		return this.request({
			url: '/resume/myCandidate',
			data: {
				pageNum,
				pageSize,
				status, 
				keyWord
			}
		})
	}
}

export default ResumeModel