import { HTTP } from '../utils/http.js'

class PostModel extends HTTP {
	constructor(arg) {			
	    super()
	}
	// 
	jobList(pageNum, pageSize, keyWord, label){
		return this.request({
			url: '/job/jobList',
			data: {
				pageNum, 
				pageSize, 
				keyWord, 
				label	
			}
		})
	}
	//
	jobDetail(id){
		return this.request({
			url: '/job/jobDetail',
			data: {
				id	
			}
		})
	}
	// A
	collectionJob(id){
		return this.request({
			url: '/job/collectionJob',
			data: {
				id	
			}
		})
	}
	// 
	collectionJobList(pageNum=1, pageSize=10, collectionJobStatus){
		return this.request({
			url: '/job/collectionJobList',
			data: {
				pageNum,
				pageSize,
				collectionJobStatus
			}
		})
	}
	
	/********** B *************/
	
	//   B
	publishJobList(pageNum=1, pageSize=10, status=0, jobName=''){
		return this.request({
			url: '/job/publishJobList',
			data: {
				pageNum,
				pageSize,
				jobName,
				status
			}
		})
	}
	// /
	saveJob(
		/* id, 
		industryCode, // 
		jobName, // 
		description, // 
		num, // 
		educationDegree, //  0123
		workExperience, //  0123
		probation, // 01
		trialTime, // 
		trialSalary, // 
		officialSalary, // 
		avgSalary, // 
		reward, // 
		overtime //  */
		
		id,
		jobName,
		description,
		num,
		minAge,
		maxAge,
		educationDegree,
		workExperience,
		probation,
		trialTime,
		trialSalary,
		officialSalary,
		interviewTime,
		avgSalary,
		reward,
		overtime,
		totalAmount, // 
	){
		return this.request({
			url: '/job/saveJob',
			data: {
				id,
				jobName,
				description,
				num,
				minAge,
				maxAge,
				educationDegree,
				workExperience,
				probation,
				trialTime,
				trialSalary,
				officialSalary,
				interviewTime,
				avgSalary,
				reward,
				overtime,
				totalAmount,
			}
		})
	}
	// 
	// status 0  1  2 
	updateStatus(id, status){
		return this.request({
			url: '/job/updateStatus',
			data: {
				id,
				status
			}
		})
	}
	// 
	getArea(parentCode="root"){
		return this.request({
			url: '/job/getArea',
			data: {
				id,
				parentCode
			}
		})
	}
	// 
	getIndustry(){
		return this.request({
			url: '/job/getIndustry',
			data: {}
		})
	}
}

export default PostModel