import { HTTP } from '../utils/http.js'

// promise

class UserModel  extends HTTP {
	constructor(arg) {			
	    super()
	}
	// codetoken
	wxLogin(code){
		return this.request({
			url: '/login/wxLogin',
			method: 'POST',
			data: {
				code	
			}
		})
	}
	// 
	getBindPhoneNumber(encryptedData, iv){
		return this.request({
			url: '/login/getPhoneNumber',
			method: 'POST',
			data: {
				encryptedData,
				iv
			}
		})
	}
	// 
	fastLogin(phoneNumber){
		return this.request({
			url: '/login/fastLogin',
			method: 'POST',
			data: {
				phoneNumber
			}
		})
	}
	// 
	getVerifyCode(phone, type){
		let url = null
		if(type === 'A'){
			url = '/login/sendVerdifyCode'
		}else{
			url = '/login/sendVerdifyCodeForB'
		}
		return this.request({
			url,
			method: 'POST',
			data:{
				phone
			}
		});
	}
	// 
	verifyCode(phone, code){
		return this.request({
			url: '/login/register',
			method: 'POST',
			data:{
				phone,
				code
			}
		})
	}
	// 
	register(name, age){
		return this.request({
			url: '/login/register',
			methods: 'POST',
			data: {
				name,
				age
			}
		})
	}
	// 
	login(phone, code, type){
		let url = null
		if(type === 'A'){
			url = '/login/loginByCode'
		}else{
			url = '/login/loginByCodeforB'
		}
		return this.request({
			url,
			methods: 'POST',
			data: {
				phone,
				code
			}
		})	
	}
	// 
	getUserInfo(){
		return this.request({
			url: '/user/getUserInfo'
		})
	}
	//
	updateUserInfo(name, age){
		return this.request({
			url: '/user/updateUserInfo',
			data: {
				name,
				age
			}
		})
	}
	// 
	cashout(){
		return this.request({
			url: '/user/cashout',
		})
	}
	//  B
	getStoreInfo(){
		return this.request({
			url: '/user/getStoreInfo',
		})
	}
	
	//  B
	getCashoutDetail(){
		return this.request({
			url: '/user/getCashoutDetail'
		})
	}
	
	// B
	loginForB(){
		return this.request({
			url: '/login/loginForB'
		})
	}
}

export default UserModel